// --- src/app/auth.service.ts ---
import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Router } from '@angular/router';

@Injectable()
export class AuthService {
  // private baseUrl = 'http://localhost:3000';
  private baseUrl = 'https://portfolio-backend-7r6r.onrender.com/api/auth';


  constructor(private http: HttpClient, private router: Router) {}

 login(credentials: { email: string, password: string }) {
  return this.http.post<{ token: string }>(`${this.baseUrl}/login`, credentials);
}

signup(formData: FormData) {
  return this.http.post(`${this.baseUrl}/signup`, formData);
}

  storeToken(token: string) {
    localStorage.setItem('token', token);
  }

  getToken() {
    return localStorage.getItem('token');
  }

  logout() {
    localStorage.removeItem('token');
    this.router.navigate(['/login']);
  }

  isLoggedIn(): boolean {
    return !!localStorage.getItem('token');
  }
  getProfile() {
  return this.http.get('https://portfolio-backend-7r6r.onrender.com/api/auth/profile');
}

}
